import React, { useState } from "react";
import "./Mockup.css";
import { Form, Stack, Spinner, Alert, Table } from "react-bootstrap";
import CustomButton from "../../components/Button/Button";
import FileUpload from "../../components/FileUploader/FileUploader";
import CustomModal from "../../components/Modal/customModal";
import { downloadTemplate } from "../../utils/downloadTemplate";
import useToast from "../../hooks/useToast";
import Loader from "../../components/Loader/Loader";
import ErrorPopup from "../../components/ErrorPopup/ErrorPopup";
import useMassUpload from "../../hooks/Mass-Upload/useMassUpload";
import MappedList from "./mappedList";
import validateFile from "./validateFile";
import CustomSelect from "../../components/Select/customSelect";

const MassUpload = () => {
  const [modalShow, setModalShow] = useState(false);
  const [errorModalShow, setErrorModalShow] = useState(false);
  const [showContentErrors, setShowContentErrors] = useState(false);
  const [showSpreadsheetModal, setShowSpreadsheetModal] = useState(false); // New state for spreadsheet modal
  const { showErrorToast, showSuccessToast } = useToast();
  const [isValidating, setIsValidating] = useState(false);
  const [operationChoice, setOperationChoice] = useState("");
  const [errors, setErrors] = useState([]);
  const [columnHeaders, setColumnHeaders] = useState([]);
  const [mandatoryAttributes, setMandatoryAttributes] = useState([]);

  const handleFileUpload = async (files) => {
    if (files.length === 0) return;
    try {
      setIsValidating(true);
      console.log("Starting file validation...");
      // ‚úÖ Await validation
      const {
        headers = [],
        validationErrors = [],
        mandatoryAttributes = [],
      } = await validateFile(files[0]);
      console.log("Extracted Headers:", headers);
      console.log("Validation Errors:", validationErrors);
      console.log(
        "MassUpload - Received mandatory attributes:",
        mandatoryAttributes
      );
      setColumnHeaders(headers);
      setMandatoryAttributes(mandatoryAttributes);

      if (validationErrors.length === 0) {
        setErrors([]); // ‚úÖ Clear previous errors
        showSuccessToast("No Error! File validated successfully!");
        // Enable submit button here
      } else {
        setErrors(validationErrors); // ‚úÖ Store validation errors in state
        console.warn("Validation Errors:", validationErrors);
        setErrorModalShow(true);
      }
    } catch (errorResponse) {
      console.error("Validation failed:", errorResponse.errors);
      setErrors(errorResponse.errors || ["Unknown validation error."]);
      setColumnHeaders(errorResponse.headers || []);
      setMandatoryAttributes(errorResponse.mandatoryAttributes || []);
      setErrorModalShow(true);
    } finally {
      setIsValidating(false);
    }
  };

  const handleDownloadTemplate = () => {
    downloadTemplate(showErrorToast)(operationChoice);
  };

  const handleReset = () => {
    // Reset all states to their initial values
    setModalShow(false);
    setErrorModalShow(false);
    setShowContentErrors(false);
    setShowSpreadsheetModal(false); // Also reset the spreadsheet modal state
    setIsValidating(false);
    setOperationChoice("");
    setErrors([]);
    setColumnHeaders([]);
    setMandatoryAttributes([]);
    // Reset the dropdown
    const selectElement = document.querySelector(
      'select[aria-label="Choose Operations"]'
    );
    if (selectElement) {
      selectElement.value = "";
    }

    // Show toast message
    showSuccessToast("Widget reset successfully!");
  };

  const handleOpenSpreadsheetModal = () => {
    setShowContentErrors(false); // Close content errors modal
    setShowSpreadsheetModal(true); // Open spreadsheet modal
  };

  const handleOperationChange = (value) => {
    console.log("Operation selected:", value); // Debug log
    setOperationChoice(value);
  };

  // Add this useEffect to monitor state changes
  React.useEffect(() => {
    console.log("Operation Choice updated:", operationChoice);
  }, [operationChoice]);

  // It will be disabled if no file is uploaded (no headers) or if there are validation errors.
  const submitDisabled = columnHeaders.length === 0 || errors.length > 0;

  console.log("Operation Choice:", errors);

  return (
    <>
      <Stack className="mt-3" gap={4}>
        {/* Choose operation and download template */}
        <Stack direction="horizontal">
          <CustomSelect
            selectedValue={operationChoice}
            onChange={handleOperationChange}
            size="lg"
            className="w-50"
            options={{
              defaultLabel: "Choose Operations",
              list: [
                { value: "1", label: "Physical Product/Raw Material" },
                { value: "2", label: "Physical Product Structure" },
                { value: "3", label: "Document" },
                { value: "4", label: "Physical Product-Document" },
              ],
            }}
          />
          {/* Conditionally render multiple file input for Document type */}¬†
          <div className="p-2 ms-auto">
            {operationChoice === "3" && (
              <Form.Group controlId="formFileMultiple">
                <Form.Control type="file" multiple />
              </Form.Group>
            )}
          </div>
          <div className="p-2">
            <CustomButton
              variant="link ms-auto"
              size="lg"
              onClick={handleDownloadTemplate}
              text="Download Template"
            />
          </div>
        </Stack>

        {/* Drag and Drop File Upload */}
        <FileUpload
          fileTypes={["XLSX"]}
          multiple={false}
          onUpload={handleFileUpload}
          onReset={handleReset}
        />

        {/* Show loader while validating */}
        {isValidating && <Loader />}

        {/* Submit Button and Content Error Button */}
        <Stack direction="horizontal" gap={2}>
          <Form.Check
            type="checkbox"
            label="Background"
            className="size-increase"
          />
          <div className="ms-auto d-flex gap-5">
            {errors.length > 0 && (
              <CustomButton
                variant="danger"
                onClick={() => setShowContentErrors(true)}
                text={`Content Errors (${errors.length})`}
              />
            )}

            <CustomButton
              variant="info"
              onClick={handleOpenSpreadsheetModal}
              text="Manage Spreadsheet Columns"
            />

            <CustomButton

              variant={submitDisabled ? "secondary" : "primary"}
              disabled={submitDisabled}
              size="lg"
              onClick={() => setModalShow(true)}
              text="Submit"
            />
          </div>
        </Stack>
      </Stack>

      {/* Content Errors Modal */}
      <CustomModal
        show={showContentErrors}
        onHide={() => setShowContentErrors(false)}
        title="Content Errors"
        footerButtons={[
          {
            label: "Close",
            variant: "danger",
            onClick: () => setShowContentErrors(false),
          },
        ]}
      >
        <ErrorPopup errors={errors} />
      </CustomModal>

      {/* Confirmation Modal */}
      <CustomModal
        show={modalShow}
        onHide={() => setModalShow(false)}
        title="Custom Modal Title"
        footerButtons={[
          {
            label: "Cancel",
            variant: "danger",
            onClick: () => setModalShow(false),
          },
          {
            label: "Confirm",
            variant: "success",
            onClick: () => alert("Confirmed!"),
          },
        ]}
      >
        <p>This is a reusable modal component with dynamic content.</p>
      </CustomModal>

      {/* Column Mapping */}
      <CustomModal
        show={showSpreadsheetModal} // Control visibility with showSpreadsheetModal
        onHide={() => setShowSpreadsheetModal(false)} // Hide the modal
        title="Manage Spreadshet Column"
        footerButtons={[
          {
            label: "Ok",
            variant: "primary",
            onClick: () => setShowSpreadsheetModal(false), // close spreadsheet modal
          },
          {
            label: "Cancel",
            variant: "danger",
            onClick: () => setShowSpreadsheetModal(false), // close spreadsheet modal
          },
        ]}
      >
        <p>
          All column(s) of uploaded Spreadsheet are mapped to valid Attribute
          Name. All mandatory Attribute Name is mapped to uploaded Spreadsheet
          Column Name
        </p>
        <MappedList
          columnHeaders={columnHeaders}
          mandatoryAttributes={mandatoryAttributes}
        />
      </CustomModal>
    </>
  );
};

export default MassUpload;
